<template>
  <div class="Chat">
    <div class="grid-3-center grid-noGutter">
      <div class="col-12">
          <div class="header">
            <div 
                v-shortkey="['esc']" @shortkey="handleCloseClick()"
                @click="handleCloseClick()">
              <p>(Ctrl+C or Esc)</p>
              <i class="fas fa-times close"></i>
            </div>
          </div>
          
          <!--
          <div class="scroll-viewport" v-infinite-scroll="loadMoreMessages" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
            <b-list-group >
              <b-list-group-item v-for="item in messages" v-html="item.contents" :key="item.id"></b-list-group-item>
            </b-list-group>  
            <button class="form-control" @click="loadMoreMessages()">Load More</button>
          </div>
          -->
          <ChatList :messages="messages"></ChatList>
          
          
          <input type="text"/>
          <div class="footer">
              <label>Enter to send</label>
          </div>
          
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import ChatList from './ChatList'
  
  export default {
    name: 'Chat',
    notifications: {
      validationFailMessage: {
        type: VueNotifications.types.warn,
        title: 'Ups',
        message: 'Form validation fail'
      },
      showCreateSuccessNotification: {
        type: VueNotifications.types.success,
        title: 'You did it!',
        message: 'That\'s the success, you created an event!'
      }
    },
    data () {
      return {
        
      }
    },
    computed: {
      messages () {
        return this.$store.getters.chatMessages
      }
    },
    mounted () {
    },
    beforeDestroy () {
      
    },
    methods: {
     handleCloseClick () {
       this.$emit('close')
     }
    },
    components: {
      ChatList
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

@import '../styles/color-variables.scss';
  
  .header{
    width: 100%;
    display: block;
    height: 50px;
  }
  .header p {
    width: 200px;
    display: inline-block;
    position: absolute;
    height: 50px;
    line-height: 50px;
    margin: 0px;
    padding: 0px;
    text-align: right;
    color: $secondary-color;
    right: 40px;
  }
  .close{
       float: right;
    color: white;
    font-size: 21px;
    margin-top: 16px;
    margin-right: 10px;
  }
  input:focus,input:active{
    outline:0px;
    box-shadow:0px;
  }
  input{
    min-width:100%;
    line-height:20px;
    padding:10px;
    height:40px;
    @media only screen and (min-width : 480px) {
      
    }
    @media only screen and (min-width : 968px) {
      
    }
  }
  label{
   float:right; 
   color:white;
   padding:10px;
   line-height:20px;
   margin:0px;
  }
  .Chat {
    position: fixed;
    z-index: 200;
    top: 0px;
    max-width: 1200px;
    background: $primary-color;
    left: 50%;
    transform: translateX(-50%);
    min-width: 250px;
    @media only screen and (min-width : 480px) {
      min-width: 480px;
    }
    @media only screen and (min-width : 968px) {
      min-width: 968px;
    }

  }
</style>



