<template>
  <div class="addEvent">
    <div class="grid-3-center grid-noGutter">
      <div class="col-12">
          
          <button v-show="!isOpen" @click="toggleChatHandler()" class="form-control"
          
          v-shortkey="['ctrl', 'c']" @shortkey="toggleChatHandler()">Open Chat (Ctrl+C)</button>
          
          <Chat v-show="isOpen" v-on:close="toggleChatHandler"></Chat>
          
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import Chat from './Chat'

  export default {
    name: 'CityChat',
    notifications: {
      validationFailMessage: {
        type: VueNotifications.types.warn,
        title: 'Ups',
        message: 'Form validation fail'
      },
      loggedStateRequiredNotification: {
        type: VueNotifications.types.warn,
        title: 'Stop right there amigo!',
        message: 'Chat require you to be logged'
      }
    },
    data () {
      return {
      }
    },
    computed: {
      isOpen () {
        return this.$store.getters.chatIsOpen
      },
      userIsLogged () {
        return this.$store.getters.userIsLogged
      }
    },
    methods: {
      toggleChatHandler() {
        if (!this.opened && !this.userIsLogged) {
          this.loggedStateRequiredNotification()
        }
        else {
          this.$store.commit('chatOpenChange', !this.isOpen)
        }
      }
    },
    components: {
      Chat
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  button {
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 200px;
  }
</style>



