<template>
  <div class="events">
   
    <div class="grid-3-center grid-noGutter-equalHeight">
      <div class="col-4_xs-12_sm-12_md-12" v-for="item in items">
        <EventThumb v-bind:data="item"></EventThumb>
      </div>
    </div>
    
    
     <div class="grid-3-center grid-noGutter">
      <div class="col-12">
        <div class="add" @click="addEvent()">
          <p>
Créez le vôtre</p>
          <i class="far fa-calendar-plus"></i>
        </div>  
      </div>
    </div>
    
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import Vue from 'vue'
  import EventThumb from './EventThumb'

  export default {
    name: 'Events',
    notifications: {
      showLoginFailNotification: {
        type: VueNotifications.types.error,
        title: 'Some wrong happens',
        message: 'That\'s the fail, your login seems to work wrong...'
      },
      showLoginSuccessNotification: {
        type: VueNotifications.types.success,
        title: 'You did it!',
        message: 'That\'s the success, your login seems to work propertly...'
      }
    },
    data: function () {
      return {

      }
    },
    computed: {
      items: function() {
        return this.$store.getters.events.filter(e => e.status !== 'canceled')
      }
    },
    methods: {
      addEvent() {
        this.$router.push({ name: 'addevent', params: {} })
      }
    },
    components: {
      EventThumb: EventThumb
    },
    created: async function() {
      await this.$store.dispatch('listEvents')
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .add {
    border: 2px dashed black;
    max-width: 400px;
    margin: 20px auto;
  }

  .add:hover {
    cursor: pointer;
  }

  p,
  svg {
    font-size: xx-large;
    color: black;
    display: inline-block;
    padding-right: 10px;
    margin: 0px;
  }
</style>



