import config from '../config'
import fetch from 'unfetch'

var defaultHeaders = {
  'Content-Type': 'application/json'
}
var defaultAuthHeaders = {
  'Authorization': ''
}

function assignAuthHeaders (headers) {
  Object.assign(defaultAuthHeaders, headers)
}

function setHeaders (headers) {
  defaultHeaders = headers
}

function post (relativeUrl, data, opt) {
  if (!opt) opt = {}
  console.log('post', relativeUrl, data)
  var options = {
    method: 'POST',
    //  mode:"cors",
    headers: defaultHeaders,
    credentials: 'include',
    body: JSON.stringify(data)
  }
  if (opt.useAuthHeaders) Object.assign(options.headers, defaultAuthHeaders)
  return fetch(config.endpoint + '/' + relativeUrl, options)
}

function get (relativeUrl, opt) {
  if (!opt) opt = {useAuthHeaders: false}
  console.log('get', relativeUrl)
  var options = {
    method: 'GET',
    //  mode:"cors",
    headers: Object.assign({}, defaultHeaders),
    credentials: 'include'
  }
  if (opt.useAuthHeaders) Object.assign(options.headers, defaultAuthHeaders)
  if (opt.headers) Object.assign(options.headers, opt.headers)
  return fetch(config.endpoint + '/' + relativeUrl, options)
}

export default {
  getDefaultHeaders: () => defaultHeaders,
  assignAuthHeaders,
  setHeaders,
  post: post,
  get: get
}



