import Vue from 'vue'
import Vuex from 'vuex'

import eventStore from './stores/event-store'
import userStore from './stores/user-store'
import chatStore from './stores/chat-store'

Vue.use(Vuex)

var storeCreator = (() => {
  let state = {}
  let getters = {}
  let actions = {}
  let mutations = {}
  return {
    use (storeData) {
      Object.assign(state, storeData.state)
      Object.assign(mutations, storeData.mutations)
      Object.assign(actions, storeData.actions)
      Object.assign(getters, storeData.getters)
    },
    create () {
      return new Vuex.Store({
        state,
        getters,
        actions,
        mutations
      })
    }
  }
})()

var defaultStore = {
  state: {
    router: {
      redirect: '',
      current: ''
    },
    files: [],
    events: []
  },
  mutations: {
    currentRouteChange (state, current) {
      state.router.current = current
    }
  },
  actions: {
    async syncModulesList ({state, commit}) {
      state.files = []
    }
  },
  getters: {
    routerRedirect: state => state.router.redirect,
    currentRoute: state => state.router.current,
    files: state => state.files,
    events: state => state.events
  }
}

storeCreator.use(defaultStore)
storeCreator.use(eventStore)
storeCreator.use(userStore)
storeCreator.use(chatStore)

export default storeCreator.create()



