import localforage from 'localforage'

localforage.config({
    // driver      : localforage.WEBSQL, // Force WebSQL; same as using setDriver()
  name: 'futsalparis'
    // version     : 1.0,
    // size        : 4980736, // Size of database, in bytes. WebSQL-only for now.
    // storeName   : 'futsalparis', // Should be alphanumeric, with underscores.
    // description : 'some description'
})

function isProduction () {
  return window.location.href.indexOf('c9users') === -1
}

const config = {
  development: {
    endpoint: 'https://foot-shedules-javoche.c9users.io:8081',
    fbAppId: 1508142575970574
  },
  production: {
    endpoint: 'https://futsal-project.herokuapp.com',
    fbAppId: 146382732687768
  }
}

console.info('isProduction', isProduction())

var exportData = Object.assign({}, isProduction() ? config.production : config.development)
export default exportData

export var fbAppId = exportData.fbAppId



