<template>
  <div class="addEvent">
    <div class="grid-3-center grid-noGutter">
      <div class="col-12">
          
          <div class="form">
            <div class="grid-1">
              <div class="col-12">
                <h3 v-text="title"></h3>
              </div>
              <div class="col-6">
                <label>When</label>
              </div>
              <div class="col-6">
                <DateTimePicker id="when" v-model="when" ></DateTimePicker> 
              </div>
            </div>  
            <div class="grid-1">
              <div class="col-6">
                <label>Address</label>
              </div>
              <div class="col-6">
                <vue-google-autocomplete :disabled="disableInputs()" id="address" :country="['fr']" classname="form-control" placeholder="Start typing" 
                :value="address.formattedAddress"
                v-on:placechanged="getAddressData"></vue-google-autocomplete>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-6">
                <label>Max. Players</label>
              </div>
              <div class="col-6">
                <b-form-input v-model="maxPlayers" :disabled="disableInputs()"
                  type="number"
                  placeholder="Players capacity (in the field)"></b-form-input>
              </div>
            </div> 
            
             <div class="grid-1">
              <div class="col-6">
                <label>Contact details</label>
              </div>
              <div class="col-6">
                <b-form-textarea  v-model="notes" :disabled="disableInputs()"
                  type="number"
                  placeholder="Organizer contact details (phone, email, etc)"
                   :rows="3"
                   :max-rows="6"
                  ></b-form-textarea>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-6">
                <label>Players</label>
              </div>
              <div class="col-6">
                <b-list-group>
                  <b-list-group-item v-for="item in players" :key="item.id" v-text="item.name"></b-list-group-item>
                </b-list-group>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-6">
                <label>Created by</label>
              </div>
              <div class="col-6">
                <b-form-input :value="createdBy" :disabled="true" type="text"></b-form-input>
              </div>
            </div> 
            
            
            <div class="grid-1">
              <div class="col-12">
                <hr>
                <b-button type="submit" variant="primary" @click="validateFields()">Update</b-button>
                <b-button v-cloak type="button" variant="success" v-show="userCanJoin()" @click="join()">Join</b-button>
                <b-button type="button" variant="warning" v-show="canCancel()" @click="cancel()">Cancel Event</b-button>
                <b-button type="button" variant="warning" v-show="canUnCancel()" @click="cancel(true)">Resume Event</b-button>
              </div>
            </div>
          </div>
          
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import DateTimePicker from './DateTimePicker'
  import VueGoogleAutocomplete from 'vue-google-autocomplete'
  import moment from 'moment'

  export default {
    name: 'EventDetails',
    notifications: {
      validationFailMessage: {
        type: VueNotifications.types.warn,
        title: 'Ups',
        message: 'Form validation fail'
      },
      showUpdateSuccessNotification: {
        type: VueNotifications.types.success,
        title: 'You did it!',
        message: 'Update is on the way!'
      }
    },
    data() {
      return {
        id: '',
        status: '',
        owner: null,
        maxPlayers: 10,
        when: '',
        notes: '',
        address: {
          formattedAddress: '',
          country: '',
          city: '',
          postalCode: '',
          latitude: '',
          longitude: ''
        },
        players: []
      }
    },
    computed: {
      createdBy () {
        return this.owner && this.owner.name
      },
      title() {
        return 'Match details' + (this.status === 'canceled' ? ' (Canceled)' : '')
      },
      items: function() {
        return this.$store.getters.events
      }
    },
    methods: {
      disableInputs() {
        return this.whenIsBeforeNow() || !this.isOwner()
      },
      isOwner() {
        return this.owner && this.$store.getters.loggedUserId == this.owner._id
      },
      cancel(isUnCancel) {
        if(isUnCancel){
          this.status = 'created'
        }
        else{
          this.status = 'canceled'
          this.hasCanceled = true
        }
      },
      canUnCancel () {
        return this.status === 'canceled' && this.hasCanceled
      },
      canCancel() {
        return this.isOwner() && this.status === 'created' &&
          this.players.filter(p => p && p._id !== this.$store.getters.loggedUserId).length === 0
      },
      join: function() {
        this.players.push({
          _id: this.$store.state.user.id,
          name: this.$store.state.user.name,
          email: this.$store.state.user.email
        })
      },
      userCanJoin: function() {
        return this.players.filter(p => p._id == this.$store.getters.loggedUserId).length === 0 && !this.whenIsBeforeNow()
      },
      getAddressData: function(address, addressComponents) {
        this.address.formattedAddress = addressComponents.formatted_address
        this.address.city = address.locality
        this.address.country = address.country
        this.address.postalCode = address.postal_code
        this.address.latitude = address.latitude
        this.address.longitude = address.longitude
      },
      whenIsBeforeNow() {
        return moment(new Date(this.when)).isBefore(moment())
      },
      validateFields: async function() {

        if (this.$store.getters.loggedUserId == this.owner._id && !this.whenIsBeforeNow()) {
          if (this.maxPlayers < 7 || this.maxPlayers > 14) return this.validationFailMessage({ message: 'Max. Players should be 8 to 14' })
          if (!this.address.formattedAddress) return this.validationFailMessage({ message: 'Address is required' })
          if (!this.when) return this.validationFailMessage({ message: 'When is required' })
          await this.$store.dispatch('updateEvent', JSON.parse(JSON.stringify(this.$data)))
          this.showUpdateSuccessNotification()
        }

        if (this.whenIsBeforeNow()) {
          this.status = "archived"
          await this.$store.dispatch('updateEvent', {status: 'archived',id: this.id, owner: this.owner})
        }

        this.$router.push({
          name: 'welcome',
          params: {}
        })
      },
      load: async function() {
        let event = await this.$store.dispatch('getEvent', { _id: this.$router.currentRoute.params._id })
        Object.keys(event).forEach(k => this[k] = event[k])
      },
    },
    components: {
      DateTimePicker,
      VueGoogleAutocomplete
    },
    created: async function() {
      this.load()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .form {
    max-width: 700px;
    min-width: 260px;
    margin: 0 auto;
    background: snow;
    padding: 50px 50px;
    color: #2c3e50;
  }
</style>



