<template>
  <b-list-group-item class="ChatListItem">
    <p v-html="data.contents"></p>
    <hr>
    <span v-html="signature"></span>
  </b-list-group-item>
</template>

<script>
  /* eslint-disable */
  import moment from 'moment'

  export default {
    name: 'ChatListItem',
    props: ['data'],
    data() {
      return {
      }
    },
    computed: {
      signature () {
        //return `from ${this.data.author} at ${moment(new Date(this.data.created)).format('DD/MM/YYYY HH:mm:ss')}`
        return `from ${this.data.author}, ${moment(new Date(this.data.created)).fromNow()}`
      }
    },
    methods: {
     
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  span{
    font-size:small;
    float:right;
    text-align:right
  }
</style>



