/* eslint-disable */
import ajax from '../helpers/ajax'
import router from '../router'

export var state = {
  
}

export var mutations = {
  eventsFetchSuccess (state, events) {
    state.events = events
  },
}

export var actions = {
  createEvent: async function ({state, commit}, data) {
    let rta = await ajax.post('v1/events/', data, {useAuthHeaders: true})
    if (rta.status == 201) rta = await rta.json()
    return data;
  },
  updateEvent: async function ({state, commit}, data) {
    let rta = await ajax.post('v1/events/' + data.id, data, {useAuthHeaders: true})
    if (rta.status == 201) rta = await rta.json()
    return data;
  },
  listEvents: async function ({state, commit}, data) {
    let rta = await ajax.get('v1/events/', {useAuthHeaders: true})
    if (rta.status == 200) rta = await rta.json()
    console.log('RTA',rta)
    commit('eventsFetchSuccess', rta)
    return data;
  },
  getEvent: async function ({state, commit}, {_id}) {
    let rta = null // state.events.filter(e => e._id.toString() == _id.toString())[0]
    if (!rta){
      rta = await ajax.get('v1/events/'+_id, {useAuthHeaders: true})
      if (rta.status == 200) rta = await rta.json()
      console.log('getEvent', rta)
    }
    return rta;
  }
}

export var getters = {
  
}

export default {
  state,
  mutations,
  actions,
  getters
}




