<template>
  <div class="event-thumb" @click="openEvent()">
    <div class="grid-1 grid-noGutter">
      <div class="col">
        <img src="../assets/imageholder.png"/>
        <div>
          <p class="players" v-html="mainText"></p>
          <p class="title" v-html="title"></p>
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import VueNotifications from 'vue-notifications'

  import moment from 'moment'
  /* eslint-disable */


  export default {
    name: 'EventThumb',
    notifications: {
      showLoginFailNotification: {
        type: VueNotifications.types.error,
        title: 'Some wrong happens',
        message: 'That\'s the fail, your login seems to work wrong...'
      },
      showLoginSuccessNotification: {
        type: VueNotifications.types.success,
        title: 'You did it!',
        message: 'That\'s the success, your login seems to work propertly...'
      }
    },
    props: ['data'],
    data() {
      return {

      }
    },
    computed: {
      time: function() {
        return moment(new Date(this.data.when)).format('DD/MM [at] HH[h]mm')
      },
      title: function() {

        return `${this.data.address.formattedAddress}, ${this.time}`
      },
      mainText: function() {
        if (this.data.status === 'archived') {
          return 'Already played'
        }
        return `${this.data.players.length}/${this.data.maxPlayers} - ${this.data.maxPlayers-this.data.players.length} spots free`
      }
    },
    methods: {
      openEvent: function() {
        this.$router.push({
          name: 'editevent',
          params: {
            _id: this.data.id
          }
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .title {
    color: white;
    font-size: 18px;
    margin-top: 0px;
    background: black;
    margin-bottom: 0px;
    min-height: 80px;
    line-height: 30px;
    padding-top: 10px;

  }

  .players {
    color: black;
    font-size: 20px;
  }

  img {
    max-width: 260px;

  }

  .col {
    position: relative;
  }

  .event-thumb:hover {
    cursor: pointer;
  }

  .event-thumb {
    /*background: url('../assets/imageholder.png');
    background-repeat: no-repeat;
    background-size: contain;*/
    min-width: 260px;
    min-height: 200px;
    background-color: white;
  }
</style>



