<template>
  <div class="addEvent">
    <div class="grid-3-center grid-noGutter">
      <div class="col-12">
          
          <div class="form">
            <div class="grid-1">
              <div class="col-12">
                <h3>New Match</h3>
              </div>
              <div class="col-6">
                <label>When</label>
              </div>
              <div class="col-6">
                <DateTimePicker id="when" v-model="when" value=""></DateTimePicker> 
              </div>
            </div>  
            <div class="grid-1">
              <div class="col-6">
                <label>Address</label>
              </div>
              <div class="col-6">
                <vue-google-autocomplete id="address" :country="['fr']" classname="form-control" placeholder="Start typing" v-on:placechanged="getAddressData"></vue-google-autocomplete>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-6">
                <label>Max. Players</label>
              </div>
              <div class="col-6">
                <b-form-input v-model="maxPlayers"
                  type="number"
                  placeholder="Players capacity (in the field)"></b-form-input>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-6">
                <label>Contact details</label>
              </div>
              <div class="col-6">
                <b-form-textarea  v-model="notes"
                  type="number"
                  placeholder="Organizer contact details (phone, email, etc)"
                   :rows="3"
                   :max-rows="6"
                  ></b-form-textarea>
              </div>
            </div> 
            
            <div class="grid-1">
              <div class="col-12">
                <hr>
                <b-button type="submit" variant="primary" @click="validateFields()">Create</b-button>
              </div>
            </div>
          </div>
          
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import DateTimePicker from './DateTimePicker'
  import VueGoogleAutocomplete from 'vue-google-autocomplete'

  export default {
    name: 'AddEvent',
    notifications: {
      validationFailMessage: {
        type: VueNotifications.types.warn,
        title: 'Ups',
        message: 'Form validation fail'
      },
      showCreateSuccessNotification: {
        type: VueNotifications.types.success,
        title: 'You did it!',
        message: 'That\'s the success, you created an event!'
      }
    },
    data () {
      return {
        notes: '',
        maxPlayers: 10,
        when: '',
        address: {
          formattedAddress: '',
          country: '',
          city: '',
          postalCode: '',
          latitude: '',
          longitude: '',
          
        },
        
      }
    },
    computed: {
      items: function () {
        return this.$store.getters.events
      }
    },
    methods: {
      getAddressData: function (address, addressComponents) {
        this.address.formattedAddress = addressComponents.formatted_address
        this.address.city = address.locality
        this.address.country = address.country
        this.address.postalCode = address.postal_code
        this.address.latitude = address.latitude
        this.address.longitude = address.longitude
      },
      validateFields: async function () {
        if (this.maxPlayers < 7 || this.maxPlayers > 14) return this.validationFailMessage({message: 'Max. Players should be 8 to 14'})
        if (!this.address.formattedAddress) return this.validationFailMessage({message: 'Address is required'})
        if (!this.when) return this.validationFailMessage({message: 'When is required'})
        await this.$store.dispatch('createEvent',JSON.parse(JSON.stringify(this.$data)))
        this.showCreateSuccessNotification()
        this.$router.push({
          name: 'welcome',
          params: { }
        })
      }
    },
    components: {
      DateTimePicker,
      VueGoogleAutocomplete
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .form {
    max-width: 700px;
    min-width: 260px;
    margin: 0 auto;
    background: snow;
    padding: 50px 50px;
    color: #2c3e50;
  }
</style>



