/* eslint-disable */
import ajax from '../helpers/ajax'
import router from '../router'
import userAuth from '../helpers/user-auth'
import user from '../helpers/user'
import localforage from 'localforage'
import analytics from '../helpers/analytics'
import chatStubs from '../helpers/chatStubs'


export var state = {
  chat: {
    isOpen: false,
    messages: []
  }
}

export var mutations = {
  chatOpenChange (state,isOpen) {
    state.chat.isOpen = isOpen
  },
  messagesReceived (state, {newMessages}) {
    newMessages.forEach(message => {
      state.chat.messages.push(message)
    })
  }
}

export var actions = {
  async loadLastMessages ({state, commit}, {quantity, beforeDate}) {
    let newMessages = await chatStubs.getLastMessages({quantity, beforeDate})
    if (newMessages.length===0) {
      console.warn('No messages left...')
    }
    commit('messagesReceived', {
      newMessages: newMessages
    })
    return newMessages.length>0
  }
}

export var getters = {
  chatIsOpen: (state) => state.chat.isOpen,
  chatMessages: (state) => state.chat.messages
}

export default {
  state,
  mutations,
  actions,
  getters
}





