import { fbAppId } from '../config'

const TIMEOUT_SECONDS = 10
var state = {
  initialized: false
}

function getFB (start) {
  if (!start) {
    start = Date.now()
  }
  return new Promise((resolve, reject) => {
    if (typeof window.FB === 'undefined') {
      let check = setInterval(() => {
        if (Date.now() - start > TIMEOUT_SECONDS * 1000) {
          reject(new Error('Unable to retrieve facebook analytics object'))
        }
        if (typeof window.FB !== 'undefined' && state.initialized) {
          clearInterval(check)
          resolve(window.FB)
        }
      }, 200)
    } else {
      resolve(window.FB)
    }
  })
}

export default {
  initializeAnalytics: () => {
    window.fbAsyncInit = () => {
      window.FB.init({
        appId: fbAppId,
        cookie: true, // enable cookies to allow the server to access the session
        xfbml: true, // parse social plugins on this page
        version: 'v2.8' // use graph api version 2.8
      })
      state.initialized = true
    }
    (function (d, s, id) {
      var js
      var fjs = d.getElementsByTagName(s)[0]
      if (d.getElementById(id)) return
      js = d.createElement(s)
      js.id = id
      js.src = 'https://connect.facebook.net/en_US/sdk.js'
      fjs.parentNode.insertBefore(js, fjs)
    })(document, 'script', 'facebook-jssdk')
  },
  setUserId: id => {
    getFB().then(FB => FB.AppEvents.setUserID(id))
  },
  logPageEvent: () => {
    getFB().then(FB => FB.AppEvents.logPageView())
  },
  logLoginSuccess: () => {
    getFB().then(FB => FB.AppEvents.logEvent('loginSuccess'))
  }
}



