<template>
  <div class="addEvent">
    <div class="grid-3-center grid-noGutter">
      <div class="col-12">
          <input :value="value" v-show="false">
          <input ref="input" class="form-control" />
      </div>
    </div>
  </div>
</template>

<script>
  /* eslint-disable */
  import VueNotifications from 'vue-notifications'
  import flatpickr from "flatpickr";


  export default {
    name: 'DateTimePicker',
    props: ['id', 'value'],
    notifications: {

    },
    data() {
      return {
        fp: null
      }
    },
    computed: {

    },
    methods: {

    },
    components: {

    },
    watch: {
      value: function(val, oldVal) {
        if (this.fp && isFinite(new Date(val))) {
          this.fp.setDate(new Date(val))
        }
      },
    },
    mounted: function() {
      let vm = this
      if (!this.id) throw new Error('id required')
      this.$refs.input.id = this.id
      this.fp = flatpickr("#" + this.id, {
        enableTime: true,
        dateFormat: "d-m-Y H:i",
        altInputClass: '',
        onChange: function(selectedDates, dateStr, instance) {
          console.log(selectedDates, dateStr)
          vm.$emit('input', selectedDates[0])
        },
      });

    },
    beforeDestroy() {
      this.fp.destroy()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
</style>



