<template>
  <div class="ChatList">
    <div class="grid-center grid-noGutter">
      <div class="col-12">
          <b-button variant="secondary" size="sm" class="p-1 m-1 mb-4 mx-auto d-block"  @click="scrollBottom()">scroll bottom</b-button>
      </div>
    </div>
    
      <b-list-group >
        <ChatListItem v-for="item in sortedMessages" :data="item" :key="item.id"></ChatListItem>
      </b-list-group> 
    
    <infinite-loading @infinite="loadMoreMessages" direction="top" ref="infiniteLoading">
    <span slot="no-more">
      
    </span>
  </infinite-loading>
    
  </div>
</template>

<script>
  /* eslint-disable */
  import moment from 'moment'
  import $ from 'jquery'
  import ChatListItem from './ChatListItem'
  import _ from 'lodash'
  import InfiniteLoading from 'vue-infinite-loading';
  
  export default {
    name: 'ChatList',
    props: ['messages'],
    data() {
      return {
        busy: false
      }
    },
    computed: {
      sortedMessages () {
        return _.orderBy(this.messages, ['created'], ['asc'])
      },
      oldestMessageDate() {
        let d = null
        this.messages.forEach(m => {
          let current = moment(new Date(m.created))
          if (!d || (d && d.isAfter(current))) {
            d = current
          }
        })
        return d && d._d
      }
    },
    methods: {
      scrollBottom () {
        let el = $(".ChatList")
        el.animate({ scrollTop: el.get(0).scrollHeight }, "slow");
      },
      async loadMoreMessages($state) {
        if(this.busy||!this.$store.getters.chatIsOpen) return
        this.busy = true
        let positive = await this.$store.dispatch('loadLastMessages', {
          quantity: 5,
          beforeDate: this.oldestMessageDate
        })
        $state.loaded()
        if (!positive){
          $state.complete()
        }
        this.scrollBottom()
        this.busy = false
      }
    },
    mounted () {
       this.$nextTick(() => {
        this.$refs.infiniteLoading.attemptLoad();
      })
    },
    components: {
      ChatListItem,
      InfiniteLoading
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .ChatList {
    overflow-y: scroll;
    max-height: 500px;
    min-height: 500px;
  }
</style>



