import ajax from './ajax'

export default {
  refreshToken: (email, refreshTokenValue) => {
    return ajax.post('v1/auth/refresh-token', {
      email: email,
      refreshToken: refreshTokenValue
    })
  },
  loginWithFacebook: accessToken => {
    return ajax.post('v1/auth/facebook', {
      access_token: accessToken
    })
  }
}



