import moment from 'moment'
import shortid from 'shortid'
import _ from 'lodash'

function withDelay (seconds, handler) {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve(handler())
    }, seconds * 1000)
  })
}

var messages = (() => {
  let list = []
  let dateCursor = moment()
  if (isFinite(new Date())) {
    dateCursor = moment(new Date())
      .subtract(parseInt(Math.random() * 60), 'minutes')
      .subtract(parseInt(Math.random() * 60), 'seconds')
  }
  for (var x = 0; x < 20; x++) {
    list.push({
      id: shortid.generate(),
      contents: `${dateCursor.format('DD/MM h:mm:ss')}`,
      author: `Steve Jokes`,
      created: dateCursor._d.getTime()
    })
    dateCursor = moment(dateCursor)
      .subtract(parseInt(Math.random() * 60), 'minutes')
      .subtract(parseInt(Math.random() * 60), 'seconds')
  }
  return list
})()

export default {
  async getLastMessages ({quantity, beforeDate}) {
    return withDelay(2, () => {
      messages = _.orderBy(messages, ['created'], ['asc'])
      let validDate = !!beforeDate && isFinite(new Date(beforeDate))
      let rta = _.filter(messages, data => {
        return validDate ? moment(data.created).isBefore(moment(new Date(beforeDate))) : true
      })
      rta = _.orderBy(rta, ['created'], ['desc'])
      rta = _.take(rta, quantity)
      rta = _.orderBy(rta, ['created'], ['asc'])
      return rta
    })
  }
}



