<template>
  <div id="app">
    <div class="grid-10-center nav grid-noGutter">
      <div class="col-1_md-10 logo-wrapper">
        <router-link to="/">
          <img class="logo" src="./assets/logo.png">
        </router-link>
      </div>
      <div class="col-4_md-10 logo-wrapper">
        <router-link to="/">
          <h3 class="logo-text">Futsal France</h3>
        </router-link>
      </div>
      <div class="col-5_md-10"></div>
    </div>
    
    
   <div id="app2">
      <div class="grid-10-center grid-noGutter">
      <div class="col-10">
        
          <router-view/>
        
      </div>
    </div>
    
     <div class="grid-8-center grid-noGutter">
      <div class="col-8">
        <hr>
        <p class="footer-text">Questions, Ideas, help? Write to the creator address: arancibiajav@gmail.com</p>
      </div>
    </div>
   </div>
    
   <CityChat></CityChat>
    
  </div>
</template>

<script>
  import CityChat from './components/CityChat'
  
  export default {
    name: 'app',
    components: {
      CityChat
    },
    created: async function () {
      await this.$store.dispatch('refreshToken')
      await this.$store.dispatch('updateMyProfileDetails')
      if (this.$store.getters.userIsLogged) {
        if (this.$router.currentRoute.query.redirect) {
          this.$router.push({ path: this.$router.currentRoute.query.redirect })
        }
      }
    }
  }
</script>

<style lang="scss">

  @import './styles/color-variables.scss';

  body {
    margin: 0px;
    padding: 0px;
    background: transparent;
  }

  html {

    background: url(https://images.unsplash.com/photo-1499894939374-c8d8c6107e05?auto=format&fit=crop&w=1861&q=80);
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-repeat: no-repeat;

  }

  .nav {
    padding-top: 10px;
    padding-bottom: 10px;
    background: white;

  }

  .logo-text {
    color: darkblue;
    margin-top:0px;
    position: relative;
    top: 7px;
    text-align: left;
    padding-left: 10px;
    @media only screen and (max-width: 64em) {
      margin-top:50px;
      top: 0px;
      text-align: center;
    }
  }

  .logo {
    max-width: 50px;
    float: right;
    margin: 0 auto;
    display: block;
    @media only screen and (max-width: 64em) {
      float:none;
    }
  }

  .footer-text {
    text-align: center;
    color: black;
  }

  #app2 {
    background-color: white;
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 50px;
    padding-bottom: 50px;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
</style>



