<template>
  <div class="hello">
    <h1>
Les match de futsal dans toute la France</h1>
    <p>
      The idea is simple.
      <br>
      Everyone can sign in using Facebook and create/join a Futsal event.
      <br>
      A Futsal event has a max. number of players, an address, a datetime, and organizers contact details.
      <br>
      There is a global comment/chat section to discuss and share prior and after matches.
      <br>
      Enjoy!
    </p>
    
    <p>
      
L'idée est simple.<br>
Tout le monde peut se connecter en utilisant Facebook et créer / rejoindre un événement Futsal.<br>
Un événement de futsal a un max. nombre de joueurs, une adresse, un datetime, et les coordonnées des organisateurs.<br>
Il y a une section globale de commentaire / discussion pour discuter et partager des matchs avant et après.<br>
Prendre plaisir!
    </p>
   
    <ul v-show="false">
      <router-link to="/login">Login</router-link>
    </ul>
    <Events></Events>
  </div>
</template>

<script>
import Events from './Events'
export default {
  name: 'Welcome',
  data () {
    return {
      msg: 'Welcome to Paris Futsal'
    }
  },
  components: {
    Events: Events
  },
  created: async function () {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



